export enum ChartType {
    BAR, LINE
}

export function getChartOptions(title: string, yLabel: string, stacked: boolean = false) {
    let options = {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true
                },
                stacked: stacked,
                scaleLabel: {
                    display: true,
                    labelString: yLabel.toLowerCase()
                }
            }]
        },
        legend: {
            display: false,
            position: "bottom"
        },
        title: {
            display: true,
            text: title.toUpperCase()
        },
        events: ['mousemove'],
        onHover: (event: any, chartElement: any) => {
            event.target.style.cursor = chartElement[0] ? 'pointer' : 'default';
        }
    }

    return options
}

export function getBarDataset(label: string, color: string, values: number[]) {
    return {
        label: label,
        backgroundColor: color,
        data: values
    }
}

export function getLineDataset(label: string, color: string, values: number[]) {
    return {
        label: label,
        fill: false,
        borderColor: color,
        backgroundColor: color,
        pointRadius: 6,
        data: values
    }
}

