import Papa from 'papaparse'
import {YEAR_LABELS} from "@/shared/—Åonstants";

export async function getData(type: IndicatorType, month: number) {
    let typePath
    if (type === IndicatorType.CRIME) {
        typePath = 'crimes'
    }
    if (type === IndicatorType.ADMINISTRATIVE) {
        typePath = 'administrative'
    }
    if (type === IndicatorType.ACCIDENT) {
        typePath = 'accident'
    }
    if (type === IndicatorType.CIVIL) {
        typePath = 'civil'
    }
    if (type === IndicatorType.ERSOP) {
        typePath = 'ersop'
    }
    if (typeof typePath === 'undefined') {
        return
    }
    return await fetch(`/data/${typePath}/m${month}.csv`)
        .then((response) => response.text())
        .then((value: string) => Papa.parse<any>(value))
        .then((result: any) => result.data)
}

export interface IndicatorValue {
    id: number
    year: string
    area: string
    value: number
}

export interface IndicatorData {
    indicator?: number
    labels: string[]
    values: number[]
}

export enum IndicatorType {
    CRIME,
    ADMINISTRATIVE,
    ACCIDENT,
    CIVIL,
    ERSOP
}

export class Data {
    private static crimeValues: Map<number, IndicatorValue[]> = new Map();
    private static administrativeValues: Map<number, IndicatorValue[]> = new Map();
    private static accidentValues: Map<number, IndicatorValue[]> = new Map();
    private static civilValues: Map<number, IndicatorValue[]> = new Map();
    private static ersopValues: Map<number, IndicatorValue[]> = new Map();

    private static parse(type: IndicatorType, data: any): IndicatorValue[] {
        if (typeof data === 'undefined') {
            return []
        }
        const values: IndicatorValue[] = []

        const republic: IndicatorValue[] = []
        const regions: IndicatorValue[] = []
        const transportRegion: IndicatorValue[] = []

        data.forEach((item: any) => {
            const value: IndicatorValue = {
                id: Number(item[2]),
                year: String(item[1]),
                area: String(item[3]),
                value: Number(item[4])
            }
            values.push(value)

            if (type === IndicatorType.CRIME || type === IndicatorType.ACCIDENT || type === IndicatorType.ADMINISTRATIVE) {
                // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ –†–µ—Å–ø—É–±–ª–∏–∫–µ
                const republicFound = republic.find(republicValue => republicValue.id === value.id && republicValue.year === value.year && republicValue.area === value.area.substr(0, 2))
                if (republicFound) {
                    republicFound.value = republicFound.value + value.value
                } else {
                    const republicValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 2),
                        value: value.value
                    }
                    republic.push(republicValue)
                }
            }

            if (type === IndicatorType.CRIME || type === IndicatorType.ACCIDENT) {
                // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ —Ä–µ–≥–∏–æ–Ω–∞–º
                const regionFound = regions.find(regionValue => regionValue.id === value.id && regionValue.year === value.year && regionValue.area === value.area.substr(0, 4))
                if (regionFound) {
                    regionFound.value = regionFound.value + value.value
                } else {
                    const regionValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 4),
                        value: value.value
                    }
                    regions.push(regionValue)
                }
            }

            // —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ —Ç—Ä–∞–Ω—Å–ø–æ—Ä—Ç–Ω–æ–º—É —Ä–µ–≥–∏–æ–Ω—É
            if (['1974', '1977', '1978'].includes(value.area.substr(0, 4))) {
                const transportRegionFound = transportRegion.find(regionValue => regionValue.id === value.id && regionValue.year === value.year)
                if (transportRegionFound) {
                    transportRegionFound.value = transportRegionFound.value + value.value
                } else {
                    const transportRegionValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: '1970',
                        value: value.value
                    }
                    transportRegion.push(transportRegionValue)
                }
            }
        })

        values.push(...transportRegion)
        values.push(...regions)
        values.push(...republic)

        return values
    }

    public static async fetchData(type: IndicatorType, month: number) {
        if (!this.isMonthFetched(type, month)) {
            let data = await getData(type, month)

            if (type === IndicatorType.CRIME) {
                this.crimeValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ADMINISTRATIVE) {
                this.administrativeValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ACCIDENT) {
                this.accidentValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.CIVIL) {
                this.civilValues.set(month, this.parse(type, data))
            }
            if (type === IndicatorType.ERSOP) {
                this.ersopValues.set(month, this.parse(type, data))
            }
        }
    }

    public static isMonthFetched(type: IndicatorType, month: number): boolean {
        if (type === IndicatorType.CRIME) {
            return this.crimeValues.has(month)
        }
        if (type === IndicatorType.ADMINISTRATIVE) {
            return this.administrativeValues.has(month)
        }
        if (type === IndicatorType.ACCIDENT) {
            return this.accidentValues.has(month)
        }
        if (type === IndicatorType.CIVIL) {
            return this.civilValues.has(month)
        }
        if (type === IndicatorType.ERSOP) {
            return this.ersopValues.has(month)
        }
        return false
    }

    public static getYearsData(type: IndicatorType, indicator: number, area: string, month: number): IndicatorData {
        const data: IndicatorData = {indicator: indicator , labels: [], values: []}

        let values = this.getValues(type, month)

        if (typeof values == 'undefined') {
            return {labels: [], values: []}
        }

        const items = values.filter(item => (item.id === indicator && item.area === area))

        YEAR_LABELS.map(year => {
            const yearItem = items.find(item => item.year === year)
            if (yearItem) {
                data.labels.push(year)
                data.values.push(yearItem.value)
            } else {
                if (year !== YEAR_LABELS[YEAR_LABELS.length - 1]) {
                    data.labels.push(year)
                    data.values.push(0)
                }
            }
        })

        return data
    }

    public static getAreasData(type: IndicatorType, indicatorId: number, year: string, parentArea: string, month: number): IndicatorValue[] {
        let values = this.getValues(type, month)

        if (typeof values == 'undefined') {
            return []
        }

        if (parentArea.length === 6) {
            return values.filter(item => (item.id === indicatorId && item.year === year && item.area === parentArea))
        } else if (parentArea.length === 4) {
            return values.filter(item => (item.id === indicatorId && item.year === year && (item.area.substring(0, 4) === parentArea || item.area === parentArea)))
        } else if (parentArea.length === 2) {
            return values.filter(item => item.id === indicatorId && item.year === year && ((item.area.substring(0, 2) === parentArea && item.area.length === 4) || item.area === parentArea))
        }

        return []
    }

    private static getValues(type: IndicatorType, month: number): IndicatorValue[] | undefined {
        if (type === IndicatorType.CRIME) {
            return this.crimeValues.get(month) || []
        }
        if (type === IndicatorType.ADMINISTRATIVE) {
            return this.administrativeValues.get(month) || []
        }
        if (type === IndicatorType.ACCIDENT) {
            return this.accidentValues.get(month) || []
        }
        if (type === IndicatorType.CIVIL) {
            return this.civilValues.get(month) || []
        }
        if (type === IndicatorType.ERSOP) {
            return this.ersopValues.get(month) || []
        }

        return undefined
    }


}
